import { AuthenticationService } from './../services/authentication.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-partner-sign-in',
  templateUrl: './partner-sign-in.component.html',
  styleUrls: ['./partner-sign-in.component.css']
})
export class PartnerSignInComponent implements OnInit {

  bootstrapstylecssUrl: string;
  appstylecssUrl: string;
  email;
  password;
  errorMessage;
  showError:boolean;
  showLoading:boolean;

  constructor(public sanitizer: DomSanitizer,private router: Router, private authService: AuthenticationService) {

    //Replacing the old css with new css
    this.sanitizer.sanitize;
    document.getElementById('lp-bootstrap-style').setAttribute('href', './assets/customecss/css/bootstrap.min.css')
    document.getElementById('lp-app-style').setAttribute('href', './assets/customecss/css/custom.css')
 
  }

  ngOnInit(): void {
   
  }


//User Signup
  Login() {

    this.showLoading= true;
    this.showError= false;
    if(!this.email)
    {
      this.showLoading= false;
      this.showError= true;
      this.errorMessage = "Please enter Email.";
    }
    else if(!this.password)
    {
      this.showLoading= false;
      this.showError= true;
      this.errorMessage = "Please enter password.";
    }    
    else
    {
      this.showLoading= true;
      this.showError= false;
      //Sign in service call
        this.authService.login(this.email, this.password).subscribe(r => {
          console.log(r);
          if (r['IsSuccessful']) {

            this.showLoading= false;
            if(r['objLoginDetails'].RoleID==4 || r['objLoginDetails'].RoleID==1 || r['objLoginDetails'].RoleID==3) {
            //Navigate to Dashboard if user type are Admin/Partner-Account-manager
              this.router.navigate(['dashboard']);
            }
            else{
            //Navigate to landing page if user type are Partner
              this.router.navigate(['partner']);
            }

          } else {
         //   alert(r['ErrorMessage']);
            this.showError= true;
            this.showLoading= false;
            this.errorMessage = r['ErrorMessage'];
          }

        }, err => {console.log(err)
          this.showError= true;
          this.showLoading= false;
          this.errorMessage = "Connection Error Please try again";
        });
      }
    }
}
